import React, { FC } from 'react';
import './WorkedAndLearnedSnap.scss';

interface WorkedAndLearnedSnapProps {}

export const N: INumberUtility = {
  clamp: (min: number, value: number, max: number) => Math.min(Math.max(min, value), max),
  rand: (min: number, max: number) => Math.floor(Math.random() * (max - min + 1) + min)
}

interface INumberUtility {
  clamp: (min: number, value: number, max: number) => number;
  rand: (min: number, max: number) => number;
}

const WorkedAndLearnedSnap: FC<WorkedAndLearnedSnapProps> = () => {  
  const [temperature] = React.useState<number>(N.rand(65, 85));
  
  return(
    <span className="workedAndLearned">
      {/* <i className="workedAndLearned-type fa-duotone fa-sun" /> */}
      {/* <span className="workedAndLearned-temperature-value">{temperature}</span>
      <span className="workedAndLearned-temperature-unit">°F</span> */}
      
      <span className="workedAndLearned-temperature-value">Portfolio</span>
      <span className="workedAndLearned-temperature-unit">⏬</span>
    </span>
  )
}

export default WorkedAndLearnedSnap;
