import React, { lazy, Suspense } from 'react';
import { IPinDigitProps } from './PinDigit';

const LazyPinDigit = lazy(() => import('./PinDigit'));

const PinDigit = (pinDigt:IPinDigitProps,props: JSX.IntrinsicAttributes & { children?: React.ReactNode; }) => (
  <Suspense fallback={null}>
    <LazyPinDigit focused={pinDigt.focused} value={pinDigt.value} {...props} />
  </Suspense>
);

export default PinDigit;
